  | 'porsche-dark-red'
  | 'porsche-light'
  | 'porsche-dark'
  | 'porsche-black'
  | 'surface-light'
  | 'surface-dark'
  | 'neutral-grey-1'
  | 'neutral-grey-2'
  | 'neutral-grey-3'
  | 'neutral-grey-4'
  | 'neutral-grey-5'
  | 'neutral-grey-6'
  | 'neutral-grey-7'
  | 'neutral-grey-8'
  | 'status-red'
  | 'status-green'
  | 'status-yellow'
  | 'state-focus';

@Component
export default class ColorBadge extends Vue {
